/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after {
  content: "";
  content: none;
}

q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

a {
  appearance: none;
  text-decoration: none;
  color: inherit;
}

button {
  background: none;
  border: none;
  color: inherit;
  font: inherit;
  cursor: pointer;
  padding: 0;
  margin: 0;
}

@font-face {
  font-family: "Line Seed";
  src: url("../assets/fonts/LINESeedKR-Rg.eot");
  src: local("@"), url("../assets/fonts/LINESeedKR-Rg.woff") format("woff"), url("../assets/fonts/LINESeedKR-Rg.woff2") format("woff2");
  font-weight: 400;
}
@font-face {
  font-family: "Line Seed";
  src: url("../assets/fonts/LINESeedKR-Th.eot");
  src: local("@"), url("../assets/fonts/LINESeedKR-Th.woff") format("woff"), url("../assets/fonts/LINESeedKR-Th.woff2") format("woff2");
  font-weight: 300;
}
@font-face {
  font-family: "Line Seed";
  src: url("../assets/fonts/LINESeedKR-Bd.eot");
  src: local("@"), url("../assets/fonts/LINESeedKR-Bd.woff") format("woff"), url("../assets/fonts/LINESeedKR-Bd.woff2") format("woff2");
  font-weight: 700;
}
.type-heading {
  position: absolute;
  left: -20px;
  top: 0;
  transform: translateX(-100%);
  font-size: 14px;
  font-weight: 700;
  color: var(--color-black);
  opacity: 0.4;
}
@media all and (max-width: 760px) {
  .type-heading {
    font-size: 13px;
    left: 0;
    top: -10px;
    transform: translateY(-100%);
  }
}
.type-main {
  font-size: 16px;
  font-weight: 700;
  color: var(--gray8);
}
@media all and (max-width: 760px) {
  .type-main {
    font-size: 14px;
  }
}
.type-normal {
  font-size: 14px;
  font-weight: 400;
  color: var(--gray6);
}
@media all and (max-width: 760px) {
  .type-normal {
    font-size: 12px;
  }
}
.type-big {
  font-size: 18px;
  font-weight: 700;
  color: var(--gray8);
}
@media all and (max-width: 760px) {
  .type-big {
    font-size: 16px;
  }
}
.type-black {
  color: var(--color-black);
}
.type-white {
  color: var(--color-black);
}

:root {
  --color-black: #1f1f1f;
  --color-white: #fcfcfc;
  --color-gray400: #4f4f4f;
  --color-gray700: #2f2f2f;
  --gray0: #ffffff;
  --gray1: #f0f2f5;
  --gray2: #f1f3f5;
  --gray3: #e1e1e6;
  --gray4: #c7c9cf;
  --gray5: #adb5bd;
  --gray6: #87909a;
  --gray7: #484f5a;
  --gray8: #2d3541;
  --gray9: #1b2026;
  --padding: 16px;
  --border-radius: 10px;
  --max-width: 740px;
  --content-width: 720px;
}
@media all and (max-width: 760px) {
  :root {
    --padding: 12px;
    --border-radius: 8px;
  }
}

.header {
  position: sticky;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
  transition: transform 0.4s ease-out;
  box-sizing: border-box;
}
.header.on {
  left: calc(var(--padding) * -1);
  right: calc(var(--padding) * -1);
}
.header.on .inner {
  border-radius: 0;
  border-color: var(--gray1);
}
.header .inner {
  display: flex;
  align-items: center;
  width: 100%;
  max-width: var(--max-width);
  margin: 0 auto;
  padding: calc(var(--padding) * 1.5) var(--padding);
  background-color: var(--gray0);
  border-bottom: 1px solid transparent;
  border-radius: 20px;
  box-sizing: border-box;
  transition: border-radius ease-out 0.3s, border-color ease-out 0.3s;
}
@media all and (max-width: 760px) {
  .header .inner {
    padding: calc(var(--padding) * 1.5);
    border-radius: 0;
    border-color: var(--gray1);
  }
}
.header .logo > h1 {
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 20px;
}
.header .logo > h1 > span {
  font-size: 16px;
  font-weight: 700;
  color: var(--gray8);
}
@media all and (max-width: 760px) {
  .header .logo > h1 > span {
    font-size: 14px;
  }
}
.header .nav {
  flex: 1;
  display: flex;
  justify-content: flex-end;
}
.header .nav .item {
  display: inline-block;
}
.header .nav .item + .item {
  margin-left: var(--padding);
}
.header .github > svg {
  width: 20px;
  height: 20px;
}
.header .github > svg .color {
  fill: var(--gray9);
}

.page-wrap {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: var(--gray1);
  overflow-y: scroll;
  z-index: 1;
}
.page-inner {
  display: flex;
  flex-direction: column;
  gap: calc(var(--padding) * 4);
  width: 100%;
  max-width: 100%;
  margin: 0 auto;
  padding: calc(var(--padding) * 4) 0;
  box-sizing: border-box;
  max-width: var(--max-width);
}
@media all and (max-width: 760px) {
  .page-inner {
    padding: 0 0 calc(var(--padding) * 4);
  }
}
.page-section {
  width: 100%;
  max-width: 100%;
  margin: 0 auto;
  box-sizing: border-box;
}
.page-title {
  padding: calc(var(--padding) * 2);
  color: var(--gray8);
  box-sizing: border-box;
  font-size: 16px;
  font-weight: 700;
  color: var(--gray8);
}
@media all and (max-width: 760px) {
  .page-title {
    font-size: 14px;
  }
}

.section-wrap {
  position: relative;
  margin: 0 auto;
  max-width: var(--content-width);
}
@media all and (max-width: 760px) {
  .section-wrap {
    margin: 0 8vw;
  }
}
.section-inner.inner-padding {
  padding: 30px 45px;
}
.section-title {
  background-color: var(--gray1);
  padding: var(--padding) calc(var(--padding) * 2);
  margin-bottom: var(--padding);
  font-size: 16px;
  font-weight: 700;
  color: var(--gray8);
  border-radius: var(--border-radius);
}
@media all and (max-width: 760px) {
  .section-title {
    font-size: 14px;
  }
}

.sticky-wrap {
  position: relative;
  width: 100%;
  height: calc(2 * var(--vh));
}
.sticky-inner {
  position: sticky;
  top: 0;
  height: var(--vh);
}

.cover {
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  z-index: 9999;
  background-color: var(--gray1);
  transition: opacity 0.5s ease-out;
  opacity: 0;
}
.cover.loading {
  opacity: 1;
}
.cover.loaded {
  display: none;
}
.cover span {
  display: block;
  font-size: 36px;
  animation: scaling 1s ease-in 0s infinite alternate;
}

@keyframes scaling {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(1.2);
  }
}
html,
body {
  padding: 0;
  margin: 0;
  overflow: hidden;
  width: 100%;
  height: 100%;
}

body {
  font-family: "Line Seed";
  font-size: 14px;
  font-weight: 400;
  line-height: 1.2;
  color: var(--gray6);
}
main {
  background-color: var(--color-white);
}

#p5 {
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 110vw;
  height: 110vh;
  z-index: 2;
  overflow: hidden;
  pointer-events: none;
}
@media all and (max-width: 760px) {
  #p5 {
    width: 105vw;
    height: 105vh;
  }
}
#p5 canvas {
  position: relative;
  width: 100%;
  height: 100%;
}

.package {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: var(--vh);
  display: flex;
  align-items: center;
  justify-content: center;
  pointer-events: none;
  z-index: 10;
}
.package-title {
  display: flex;
  width: 100px;
  height: 100px;
  align-items: center;
  justify-content: center;
  z-index: 1;
  background-color: var(--color-white);
}
@media all and (max-width: 760px) {
  .package-title {
    width: 80px;
    height: 80px;
  }
}
.package-title span {
  font-size: 13px;
  color: var(--color-black);
}
@media all and (max-width: 760px) {
  .package-title span {
    font-size: 12px;
  }
}
.package-bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: var(--color-white);
}
.package-bg.hide {
  opacity: 0;
}
.package-guide {
  position: absolute;
  top: 80%;
  font-size: 14px;
  color: var(--color-black);
  opacity: 0.5;
}
@media all and (max-width: 760px) {
  .package-guide {
    font-size: 12px;
  }
}
.package-guide span {
  animation: blink 2.4s infinite;
}

.banner {
  padding: 150px 0;
}
.banner .title {
  display: block;
  margin-bottom: var(--padding);
  font-size: 18px;
  font-weight: 700;
  color: var(--gray8);
  color: var(--gray8);
}
@media all and (max-width: 760px) {
  .banner .title {
    font-size: 16px;
  }
}

#top {
  display: flex;
  flex-direction: column;
  gap: 8px;
}
#top .tag {
  background-color: var(--gray0);
}

#projects {
  display: flex;
  flex-direction: column;
  gap: calc(var(--padding) * 2);
}
@media all and (max-width: 760px) {
  #projects {
    gap: var(--padding);
  }
}

#copyright {
  display: flex;
  justify-content: center;
}
#copyright .copyright {
  font-size: 14px;
  line-height: 20px;
}

.project {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 16px;
  padding: calc(var(--padding) * 2) 0;
  background-color: var(--gray0);
  border-radius: 20px;
}
@media all and (max-width: 760px) {
  .project {
    border-radius: 0;
  }
}
.project .image {
  width: 100%;
  padding: 0 var(--padding);
  box-sizing: border-box;
}
.project .image > img {
  width: 100%;
  height: auto;
  max-height: 300px;
  border: 2px solid var(--gray1);
  border-radius: 4px;
  object-fit: cover;
}
.project .intro {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  width: 100%;
  padding: 0 var(--padding);
  box-sizing: border-box;
}
.project .info {
  display: flex;
  gap: var(--padding);
}
@media all and (max-width: 760px) {
  .project .info {
    flex-direction: column;
    gap: 3px;
  }
}
.project .info .title {
  display: block;
  font-size: 14px;
  font-weight: 600;
  line-height: 20px;
  color: var(--gray8);
}
.project .info .desc {
  display: block;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
}
@media all and (max-width: 760px) {
  .project .info .desc {
    font-size: 12px;
    line-height: 16px;
  }
}
.project .preview-list {
  display: block;
  width: 100%;
  padding: var(--padding);
  box-sizing: border-box;
  overflow: hidden;
}
@media all and (max-width: 760px) {
  .project .preview-list {
    margin-bottom: 18px;
  }
}
.project .preview-list .swiper-slide {
  width: auto;
  box-sizing: border-box;
  padding-left: var(--padding);
}
.project .preview-list .swiper-slide:first-child {
  padding-left: 0;
}
.project .preview-list .video {
  display: block;
  width: 100%;
  height: 100%;
}
.project .preview-list video,
.project .preview-list img {
  display: block;
  width: auto;
  height: 350px;
  border: 4px solid var(--gray1);
  border-radius: var(--border-radius);
}
@media all and (max-width: 760px) {
  .project .preview-list video,
.project .preview-list img {
    height: 200px;
  }
}
.project .preview-list video.vert,
.project .preview-list img.vert {
  height: 520px;
}
@media all and (max-width: 760px) {
  .project .preview-list video.vert,
.project .preview-list img.vert {
    height: 380px;
  }
}

.preview-popup {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: var(--gray1);
  transform: translateY(100%);
  transition: transform 0.3s ease-out;
  overflow-y: auto;
  z-index: 1;
}
.preview-popup.active {
  transform: translateY(0);
}
.preview-popup .inner {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  max-width: var(--max-width);
  margin: 0 auto;
  background-color: var(--gray0);
}
.preview-popup .inner .top {
  padding: var(--padding);
}
.preview-popup .preview-list {
  display: none;
  grid-template-columns: 1fr 1fr;
  padding: calc(var(--padding) * 0.5);
  width: 100%;
  box-sizing: border-box;
}
@media all and (max-width: 760px) {
  .preview-popup .preview-list {
    grid-template-columns: 1fr;
  }
}
.preview-popup .preview-list.single {
  grid-template-columns: 1fr;
}
.preview-popup .preview-list.show {
  display: grid;
}
.preview-popup .preview-list .preview-item {
  display: block;
  width: 100%;
  height: auto;
  padding: calc(var(--padding) * 0.5);
  box-sizing: border-box;
}
.preview-popup .preview-list .preview-item > img {
  display: block;
  width: 100%;
  height: auto;
  border-radius: var(--border-radius);
  border: 2px solid var(--gray1);
}
.preview-popup .btn-close {
  position: relative;
  width: 36px;
  height: 36px;
  background-color: var(--gray1);
  border-radius: 50%;
  transition: background-color 0.3s ease-out;
}
.preview-popup .btn-close:hover {
  background-color: var(--gray2);
}
.preview-popup .btn-close::before, .preview-popup .btn-close::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 2px;
  height: 18px;
  background-color: var(--gray9);
  transform: translate(-50%, -50%) rotate(45deg);
}
.preview-popup .btn-close::after {
  transform: translate(-50%, -50%) rotate(-45deg);
}

.company-logo {
  width: 36px;
  height: 36px;
  border: 1px solid var(--gray2);
  border-radius: 50%;
  margin-right: var(--padding);
  user-select: none;
  pointer-events: none;
}

.tag {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  height: 32px;
  font-size: 13px;
  font-weight: 400;
  line-height: 16px;
  padding: 0 10px;
  border-radius: 8px;
  background-color: var(--gray1);
  color: var(--gray8);
}
@media all and (max-width: 760px) {
  .tag {
    font-size: 12px;
    line-height: 16px;
  }
}
.tag-list {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  margin: 0 var(--padding);
  box-sizing: border-box;
}
@media all and (max-width: 760px) {
  .tag-list {
    gap: 8px;
  }
}

.link {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 10px;
  height: 32px;
  font-size: 14px;
  line-height: 20px;
  margin: 0;
  margin-left: auto;
  background-color: var(--color-white);
  border-radius: 8px;
  transition: background-color 0.3s ease-out;
}
.link:hover, .link:focus {
  background-color: var(--gray1);
}
.link.none {
  background: none;
}

.toast {
  display: flex;
  justify-content: center;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  pointer-events: none;
  z-index: 99;
}
.toast.active .message {
  transform: translateY(calc(var(--padding) * -4));
}
.toast .message {
  display: inline-block;
  padding: calc(var(--padding) * 1.5);
  font-size: 16px;
  line-height: 20px;
  color: var(--gray1);
  background-color: var(--gray9);
  border-radius: 20px;
  text-align: center;
  transform: translateY(100%);
  transition: transform 0.3s ease-out;
}

@keyframes blink {
  0% {
    opacity: 0;
  }
  40% {
    opacity: 1;
  }
  60% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}